@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body{
  font-family: 'Raleway', sans-serif;
  width:100%;
  height:100vh;
  -ms-scroll-chaining:none;
      overscroll-behavior:none;
  margin:0;
}


#quote-and-author{
    position:absolute;
    height:130px;
    width:90%;
    top:2em;
    left:5%;
}
#quote{
    text-align:center;
    clear:both;
    color:pink;
    /* transition-duration:2s; */
    line-height:1.2em;
}
#author{
    float:right;
    color:pink;
    transition-duration:2s;
}

/* animation */

/* enter exit */
.fade-enter{
    opacity:0;
}
.fade-enter-active{
    opacity:1;
    transition:opacity 2s ease 1s;
}
.fade-exit{
    opacity:1;
}
.fade-exit-active{
    opacity:0;
    transition:opacity 2s ease;
}
.fade-exit-done{
    opacity:0;
}


#wrapper{
  background-color:pink;
  height:100vh;
  width:100%;
  margin:0;
  -ms-scroll-chaining:none;
      overscroll-behavior:none;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  background-color:pink;
  transition-duration:2s;
  overflow-x:hidden;
}

#quote-box{
  background-color:white;
  width:600px;
  height:auto;
  padding:2em;
  color:white;
  border-radius:3px;
  -webkit-animation-name:slide;
          animation-name:slide;
  -webkit-animation-duration:2s;
          animation-duration:2s;
  position:relative;
  height:300px;
  display:flex;
  flex-direction:column;
  transition-duration:1s;
}
#buttons{
  clear:both;
  margin-bottom:auto;
  margin-top:14.5em; 
  z-index:2;
  position:absolute;
}
.button{
  background-color:pink;
  border:none;
  outline:none;
  cursor:pointer;
  color:white;
  border-radius:3px;
  transition-duration:2s;
  
}
.brand{
  font-size:1.2em;
  width:2.2em;
  height:1.7em;
  padding-top:10px;
  text-align:center;
  float:left;
  margin-right:.3em;
}
#new-quote{
  font-size:1em;
  float:right;
  padding:.78em;
  margin-left:25em;
}
#footer, #footer a{
  color:white;
  text-decoration:none;
  font-size:.9em;
  margin-top:1em;
}

@-webkit-keyframes slide{
  from{
      opacity:0;
      transform:translateX(200%);
  }
  to{
      opacity:1;
      transform:translateX(0%);
  }
}

@keyframes slide{
  from{
      opacity:0;
      transform:translateX(200%);
  }
  to{
      opacity:1;
      transform:translateX(0%);
  }
}


/* animation */

/* appear */
.fade-appear{
  opacity:0;
}
.fade-appear-active{
  opacity:1;
  transition:opacity 2s ease;
}

.fade-enter{
  opacity:0;
}
.fade-enter-active{
  opacity:1;
  transition:opacity 1s ease 1s;
}
.fade-exit{
  opacity:1;
}
.fade-exit-active{
  opacity:0;
  transition:opacity 1s ease;
}
